define('dogs_map',['lib/news_special/bootstrap', 'text!../data/map_data.json', 'dogs_rollover'], function (news, data, dogsRollover) {
	
    data = JSON.parse(data);
    var pointer_on_right = false;
	var svgSupported = (!!document.createElementNS && !!document.createElementNS('http://www.w3.org/2000/svg', 'svg').createSVGRect) ? true : false;

	if (!svgSupported) {
		return;
	}

	news.$('#dogs--map--static').addClass('hidden');
	news.$('#dogs--map--svg').removeClass('hidden');
    function add_colours_to_map(area_id) {

        if (area_id in data) {
            switch (data[area_id].topFive[0][0]) {
            case 'Labrador Retriever':
                news.$('#' + area_id + ', #london_areas #' + area_id).attr('style', 'fill:#007F7F');
                break;
            case 'Staffordshire Bull Terrier':
                news.$('#' + area_id + ', #london_areas #' + area_id).attr('style', 'fill:#CC0000');
                break;
            case 'Jack Russell Terrier':
                news.$('#' + area_id).attr('style', 'fill:#FF7900');
                break;
            case 'Border Collie':
                news.$('#' + area_id).attr('style', 'fill:#689C00');
                break;
            case 'Greyhound':
                news.$('#' + area_id).attr('style', 'fill:#80004D');
                break;
            }
        }
    }

    news.$('#stroke_group g').each(function () {
        var id = news.$(this).attr('id');
        add_colours_to_map(id);
    });
});
